import React from "react";

const CustomPagination = () => {
  return (
    <div className="d-flex align-items-center pagination-m">
      <div
        className="bg-white d-flex  radius-all px-0 border-all justify-content-between sub-pag-m"
        style={{ width: "fit-content" }}
      >
        <div className="d-flex justify-content-between align-items-center px-3 py-2 border-right">
          <div className="borderHeader">
            <i className="iconU-paginationPrevMost"></i>
          </div>
          <div className="px-3 borderHeader">
            <i className="iconU-paginationPrev"></i>
          </div>
          <div className="px-3 fontSizeTitle LatoBold borderHeader pag-active">
            1
          </div>
          <div className="px-3 fontSizeTitle LatoBold borderHeader">2</div>
          <div className="px-3 fontSizeTitle LatoBold borderHeader">3</div>
          <div className="px-3 fontSizeTitle LatoBold borderHeader">4</div>
          <div className="px-3 nextPagination">
            <i className="iconU-paginationNext"></i>
          </div>
          <div className="nextPagination">
            <i className="iconU-paginationNextMost"></i>
          </div>
        </div>
        <div className="d-flex justify-content-between align-items-center px-3 py-2">
          <div className="text emptyTextField">Go to page</div>
          <div className="px-3">
            <input
              className="pag-input"
              type="text"
              style={{ width: "50px" }}
            />
          </div>
          <div className="text LatoBold">Go</div>
          <div className="pl-3 text">
            <i className="iconU-paginationNext"></i>
          </div>
        </div>
      </div>
      <div className="emptyTextField px-3">
        Showing <span className="text">1 to 16 of 16</span> entries
      </div>
    </div>
  );
};
export default CustomPagination;
